body {
  margin: 0;
}

p, h1 {
  margin: 0;
  padding: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product {
  color: #616070;
  margin-bottom: 18px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 14px;
  display: flex;
}

.card__price-label {
  color: #616070;
  height: 18px;
  font-size: 12px;
  font-weight: 400;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  align-content: center;
  width: 100%;
  height: 40px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__reviews {
  color: #060b35;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  margin-left: 1px;
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
  display: flex;
}

.stars {
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.0aa15587.svg");
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

